import db from "./firebase";
import { useEffect, useState } from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { Switch } from "@mui/material";

const DataFetch = () => {
  //default app states
  const [allResumes, setAllResumes] = useState([]);
  const [dbName, setDbName] = useState("bancotalentosFinal");
  const handledbChange = (event) => {
    setDbName(event.target.value);
    console.log(dbName);
  };
  // initialize component with function start
  useEffect(() => {
    getDataToDash();
  }, [dbName]);

  // allows get into firestoredatabase
  const getDataToDash = async () => {
    const response = db.collection(dbName);
    const data = await response.get();
    const allCollectionItems = data.docs.map((item) => item.data());
    setAllResumes(allCollectionItems);
  };
  const renderOtherXps = (extention) => {
    return extention === true ? "Sim" : "Não";
  };
  // console.log(allResumes);
  return (
    <>
      <select onChange={(event) => setDbName(event.target.value)}>
        <option value="bancotalentosFinal" selected>
          2021
        </option>
        <option value="bancotalentos_2022">2022</option>
      </select>
      <TableContainer component={Paper}>
        <Table sx={{ width: "auto" }} size="small" aria-label="a dense table">
          <TableHead>
            <TableRow>
              <TableCell>Empresario</TableCell>
              <TableCell>Data Nascimento</TableCell>
              <TableCell>Telefone Contato</TableCell>
              <TableCell>Empresa Jr</TableCell>
              <TableCell>Função na EJ</TableCell>
              <TableCell>Tem outras Xps Academicas</TableCell>
              <TableCell>Enactus</TableCell>
              <TableCell>Atletica</TableCell>
              <TableCell>Diretorio Academico</TableCell>
              <TableCell>AIESEC</TableCell>
              <TableCell>Junior Achievment</TableCell>
              <TableCell>Movimento Choice</TableCell>
              <TableCell>Outras Xps Academicas</TableCell>
              <TableCell>Experiencias Adicionais</TableCell>
              <TableCell>Universidade</TableCell>
              <TableCell>Curso</TableCell>
              <TableCell>Previsão de formatura</TableCell>
              <TableCell>Experiencias Mercado de trabalho</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {allResumes.map((row) => (
              <TableRow
                key={row.name}
                sx={{ "&:last-child td, &:last-child th": { border: 0 } }}>
                <TableCell component="th" scope="row">
                  {row.colectedData.nome}
                </TableCell>
                <TableCell>{row.colectedData.dataNascimento}</TableCell>
                <TableCell>{row.colectedData.telefoneContato}</TableCell>
                <TableCell>{row.colectedData.ejDeOrigem}</TableCell>
                <TableCell>{row.colectedData.funcaoNaEj}</TableCell>
                <TableCell>
                  {row.colectedData.outrasXpsSwitch.switchOutrasXps === true
                    ? "Sim"
                    : "Não"}
                </TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.checkBox.enactus)}
                </TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.checkBox.atletica)}
                </TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.checkBox.diretorioAcademico)}
                </TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.checkBox.aiesec)}
                </TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.checkBox.juniorAchievment)}
                </TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.checkBox.choice)}
                </TableCell>
                <TableCell>{row.colectedData.outrasXpsAcademicas}</TableCell>
                <TableCell>{row.colectedData.outrasXpsadicionais}</TableCell>
                <TableCell>{row.colectedData.universidade}</TableCell>
                <TableCell>{row.colectedData.cursoGraduacao}</TableCell>
                <TableCell>{row.colectedData.prevForm}</TableCell>
                <TableCell>
                  {renderOtherXps(row.colectedData.dadosEmpresa)}
                </TableCell>

                <TableCell>{row.protein}</TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>
    </>
  );
};

export default DataFetch;
